<template>
  <div></div>
</template>

<script>
export default {
  name: "LoadingImg",
  data() {
    return {
        count:0,
        percent:'',
        imgs:[]
    }
  },
  methods: {
    preload() {
      console.log("preload");
      this.imgs = [
        "http://www.topspeedhunter.com/uploads/banner/2021-09-06/867bdde6efadbb87d57923168829691b.png",
        "http://www.topspeedhunter.com/uploads/banner/2021-09-06/834cf96f0d967e3a15f193f1909bb828.png",
        "http://www.topspeedhunter.com/uploads/banner/2021-09-06/3ec7a845c908dca54d466e89ebe47eab.png",
        "http://www.topspeedhunter.com/uploads/banner/2021-09-06/6ced2b7023c1d8eb297a2bfe790bbb4d.png",
      ];
      for (let img of this.imgs) {
        let image = new Image();
        image.src = img;
        image.onload = () => {
          this.count++;

          let percentNum = Math.floor((this.count / this.imgs.length) * 100);
          this.percent = `${percentNum}%`;
        };
      }
    },
  },
  mounted() {
    this.preload();
  },
  watch: {
    count: function (value) {
      if (value === this.imgs.length) {
        // 可以添加一些图片加载完成的逻辑操作

        console.log("图片加载完成");
      }
    },
  },
};
</script>
