      ],
      input: {
        input1: "",
        input2: "",
        input3: "",
        input4: "",
        textarea: "",
      },
    };
  },
  methods: {
    // 子页面--查看更多
    watchMore() {
      this.$router.push({
        path: "/more",
        query: {
          navName: "合作伙伴",
        },
      });
    },
    // 滚动改变导航栏背景色
    handleScroll() {
      var scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      if (scrollTop > 600) {
        // console.log(1, this.$refs.header.opacity);
        this.$refs.floor0.style.opacity = 0.4;
      } else {
        // console.log(2, this.$refs.header.opacity);
        this.$refs.floor0.style.opacity = 0;
      }
    },
    // 楼层和高亮显示
    activeHandle(index) {
      this.activeIndex = index;
      var floor = this.$refs["floor" + index];
      document.documentElement.scrollTop = floor.offsetTop;
      // 点击楼层时也会调用关闭下拉菜单【<850px】
      this.changeClose();
    },
    // 提交输入框内容消息提醒：姓名，手机号，邮箱，公司，留言【成功/警告/失败】
    submitMessage() {
      for(let key in this.input){
        console.log(key,this.input[key]);
        console.log(this.input[key].length);
      }
      if (this.input!='') {
        this.$message({
          type: "success",
          message: "提交成功",
        });
      } else if(this.input.input1=''){
        this.$message({
          type: "warning",
          message: "信息未完善，请补充",
        });
      }
    },
    // @media为850px时显示按钮：点击按钮弹出下拉菜单
    changeOpen() {
      // let ul=this.$refs.slideList;
      // this.switchButton=false;
      this.AA1 = true;
      this.openAnimation = true;
      this.AA2 = false;
      console.log(1);
    },
    changeClose() {
      // this.switchButton=true;
      this.AA2 = true;
      this.closeAnimation = true;
      this.AA1 = false;
      console.log(2);
    },
  },
  components: { SwiperVue, AddressVue, RecruitSelectVue },
  watch: {},
  computed: {},
  async mounted() {
    // wow.js滚动动画效果配置项
    new this.$wow.WOW().init({
      boxClass: "wow",

      animateClass: "animate__animated",

      offset: 0,

      mobile: true,

      live: true,
    });
    // handleScroll事件全局触发条件-scroll【监听】
    window.addEventListener("scroll", this.handleScroll);
  },
};
</script>
